import React from "react"
import * as Styled from "./styled"
import { Link } from "gatsby"
import { motion } from "framer-motion"
import { useStaticQuery, graphql } from "gatsby"
import Container from "../Container"
import Logo from "../Logo"
import EmailLink from "../EmailLink"

export default () => {

    // Site metadata query

    const data = useStaticQuery(
        graphql`
            query {
                site {
                    siteMetadata {
                        contact {
                            email
                            github
                            instagram
                            linkedin
                            twitch
                            twitter                    
                        }
                    }
                }
            }    
        `
    )

    // Animation

    const variant = {
        hidden: {
            y: "100%"
        },
        visible: {
            y: 0,
            transition: {
                duration: 0.5,
                ease: [0.85, 0, 0.35, 1],
                type: "tween"
            }
        },
        exit: {
            y: "100%",
            transition: {
                duration: 0.5,
                ease: [0.85, 0, 0.35, 1],
                type: "tween"
            }
        }
    }

    return (
        <motion.div
            initial="hidden"
            animate="visible"
            exit="exit"
            variants={variant}
        >
            <Styled.Wrapper>
                <Container hasVerticalLine={false} invertColors={true}>
                    <Styled.FooterInner>
                        <Styled.LogoCell>
                            <Link to="/">
                                <Logo invertLeft={true} />
                            </Link>
                        </Styled.LogoCell>
                        <h5><a href={data.site.siteMetadata.contact.twitter}>Twitter</a></h5>
                        <h5><a href={data.site.siteMetadata.contact.instagram}>Instagram</a></h5>
                        <h5><a href={data.site.siteMetadata.contact.github}>GitHub</a></h5>
                        <h5><a href={data.site.siteMetadata.contact.twitch}>Twitch</a></h5>
                        <h5><a href={data.site.siteMetadata.contact.linkedin}>LinkedIn</a></h5>
                        <h5>
                            <EmailLink />
                        </h5>
                    </Styled.FooterInner>
                </Container>
            </Styled.Wrapper>
        </motion.div>
    )
}