import React, { useContext } from "react"
import * as Styled from "./styled"
import { ThemeContext } from "../../contexts/ThemeContext"

const ThemeSwitcher = (props) => {
    const { theme, toggleTheme } = useContext(ThemeContext)


    // Click handler

    const handleClick = () => {
        toggleTheme()
    }
    

    return (
        <Styled.Wrapper invertRight={props.invertRight}>
            <button onClick={() => handleClick()}>
                {theme === "light" ?
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path d="M0 8a8 8 0 1116 0H0zM9.991 12H5v-2h5a3.514 3.514 0 001.447.356c.369.184.768.476 1.073.933.308.463.48 1.036.48 1.711s-.172 1.248-.48 1.71c-.305.458-.704.75-1.073.934a3.453 3.453 0 01-.97.311C10.224 15.997 10 16 10 16H8v-2h1.991s.273 0 .562-.144a.757.757 0 00.302-.255c.067-.099.145-.276.145-.601 0-.325-.078-.502-.145-.601a.757.757 0 00-.302-.255C10.263 12 9.99 12 9.99 12z"/></svg>
                :
                    <svg fill="none" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 16 16"><path d="M16 9.718A8.103 8.103 0 016.282 0 8.103 8.103 0 1016 9.718z"/></svg>
                }
            </button>
        </Styled.Wrapper>
    )
}

export default ThemeSwitcher
