import styled from "styled-components"
import { motion } from "framer-motion"

export const Wrapper = styled.div`
    background-color: ${props => props.invertColors ? props.theme.background.inverted : props.theme.background.normal };
    color: ${props => props.invertColors ? props.theme.text.inverted : props.theme.text.normal };
    margin: 0 auto;
    padding: ${props => props.isFirst ? "calc(var(--nav-height) + var(--su)) var(--su) var(--su)" : "calc(var(--su) * 2) var(--su)" };
    position: relative;

    @media (min-width: 800px) {
        padding: ${props => props.isFirst ? "var(--nav-height) calc(var(--su) * 2) var(--su)" : "var(--su) calc(var(--su) * 2)" };
    }

`

export const VerticalLine = styled(motion.div)`
    display: none;

    @media (min-width: 800px) {
        border-right: var(--line-width) solid ${props => props.invertLine ? props.theme.line.inverted : props.theme.line.normal };
        bottom: 0;
        display: block;
        height: 100%;
        left: calc(50% - var(--line-width));
        position: absolute;
        transform-origin: 50% 0%;
        top: 0;
        width: 0;
        z-index: 0;
    }

`

export const TopLine = styled(motion.div)`
    border-bottom: var(--line-width) solid ${props => props.invertLine ? props.theme.line.inverted : props.theme.line.normal };
    display: block;
    height: var(--line-width);
    position: absolute;
        left: 0;
        right: 0;
        top: 0;
    transform-origin: 50% 50%;
    z-index: 0;
`