import styled from "styled-components"

export const Wrapper = styled.div`
    background-color: ${props => props.theme.background.inverted};
    border-top: var(--line-width) solid ${props => props.theme.line.normal};
    position: relative;
    z-index: 3;
`

export const FooterInner = styled.div`
    display: grid;
    column-gap: 40px;
    row-gap: 24px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto;

    @media (min-width: 800px) {
        grid-template-columns: 3fr 1fr 1fr 1fr 1fr;
        margin-left: 0;
    }

    a,
    a:visited {
    color: ${props => props.theme.link.inverted};
    text-decoration: none;

    &:hover {
        color: ${props => props.theme.link.invertedHover};
    }


`

export const LogoCell = styled.div`
    grid-column: 1 / span 2;

    @media (min-width: 800px) {
        grid-column: auto;
        grid-row: 1 / span 2;
    }

`