import React from "react"
import * as Styled from "./styled"

const variant = {
    initial: {
        rotate: 0
    },
    hover: {
        rotate: -20,
    }
}

export default (props) => {
    return (
        <Styled.Wrapper
            whileHover="hover"
            variants={variant}
        >
            <Styled.Svg
                invertLeft={props.invertLeft}
                viewBox="0 0 40 40"
                fill="#0000FF"
                xmlns="http://www.w3.org/2000/svg"
            >
                <path d="M19.908 12.922l-3.246 9.668h6.68l-3.247-9.668h-.187z"/><path fill-rule="evenodd" clip-rule="evenodd" d="M40 0H0v40h40v-7.798a7.772 7.772 0 01-1.615.161c-3.434 0-6.024-1.875-6.024-5.414 0-4.055 3.34-5.097 6.832-5.472L40 21.39v-4.348c-1.707.183-2.712 1.147-3.092 2.254l-3.96-.563c.889-3.112 3.55-4.791 7.052-4.955V0zM15.49 26.082L13.51 32H8.868l8.449-24h5.367l8.461 24h-4.64l-1.993-5.918H15.49z" fill="#00F"/><path d="M40 24.269l-.232.032c-1.899.27-3.317.949-3.317 2.578 0 1.558 1.266 2.367 3.035 2.367.176 0 .347-.008.514-.023v-4.954z"/>
            </Styled.Svg>
        </Styled.Wrapper>
    )
}

