import styled from "styled-components"

export const Wrapper = styled.div`
    align-items: center;
    display: flex;
    justify-content: space-between;
    left: 0;
    padding: var(--su);
    position: absolute;
    right: 0;
    top: 0;
    user-select: none;
    z-index: 3;

    @media (min-width: 800px) {
        padding: 80px calc(var(--su) * 2);
    }

`

export const Left = styled.div`
    align-items: center;
    color: ${props => props.invertLeft ? props.theme.text.inverted : props.theme.text.normal };
    display: flex;
    z-index: 3;

    a[aria-current="page"] {
        cursor: default;
    }

`

export const Right = styled.div`
    align-items: center;
    display: flex;
    z-index: 3;

    a,
    a:visited,
    button {    
        color: ${props => props.invertRight ? props.theme.link.inverted : props.theme.link.normal };
        padding: 8px;

        &:hover,
        &[aria-current="page"] {
            color: ${props => props.invertRight ? props.theme.link.invertedHover : props.theme.link.hover };
        }

        &[aria-current="page"] {
            pointer-events: none;
        }
        
    }

    > *:not(:last-child) {

        @media (min-width: 800px) {
            margin-right: 4px;
        }    

    }

`
