import React from "react"
import * as Styled from "./styled"

const verticalLineVariant = {
    initial: {
        scaleY: 0
    },
    visible: {
        scaleY: 1,
        transition: {
            duration: 1
        }
    },
    exit: {
        scaleY: 0,
        transition: {
            duration: 0.5
        }
    }
}

const horizontalLineVariant = {
    initial: {
        scaleX: 0
    },
    visible: {
        scaleX: 1,
        transition: {
            duration: 1
        }
    },
    exit: {
        scaleX: 0,
        transition: {
            duration: 0.5
        }
    }
}

const Container = (props) => {
    return (
        <Styled.Wrapper className={props.className} invertColors={props.invertColors} isFirst={props.isFirst}>
            {props.children}
            {props.hasVerticalLine &&
                <Styled.VerticalLine
                    animate="visible"
                    initial={props.animateLine ? "initial" : "visible"}
                    exit="exit"
                    variants={verticalLineVariant}
                    invertLine={props.invertLine}
                />
            }
            {props.hasTopLine &&
                <Styled.TopLine
                    animate="visible"
                    initial={props.animateLine ? "initial" : "visible"}
                    exit="exit"
                    variants={horizontalLineVariant}
                    invertLine={props.invertLine}
                />
            }
        </Styled.Wrapper>
    )
}

Container.defaultProps = {
    animateLine: false,
    hasVerticalLine: false,
    invertColors: false,
    invertLine: false
}

export default Container