import styled from 'styled-components'

export const Wrapper = styled.div`

    svg {
        height: 12px;
        width: 12px;

        @media (min-width: 800px) {
            height: 16px;
            width: 16px;    
        }

        path {
            transition: fill 0.2s linear;
            fill: ${props => props.invertRight ? props.theme.link.inverted : props.theme.link.normal };
        }

    }

    &:hover svg path {
        transition: none;
        fill: ${props => props.invertRight ? props.theme.link.invertedHover : props.theme.link.hover };
    }



`