import { createGlobalStyle } from "styled-components"

const GlobalStyles = createGlobalStyle`

    // Variables

    :root {

        // Font families

        --family-heading: "DM Serif Display", sans-serif;
        --family-body: "Inter", sans-serif;

        // Line heights

        --line-height-tight: 1;
        --line-height-heading: 1.3;
        --line-height-paragraph: 1.9;
        --line-width: 1.5px;

        // Spacing units
        
        --su: 32px;
        --nav-height: 102px;

        @media (min-width: 800px) {
            --su: 54px;
            --nav-height: 206px;
        }

        @media (min-width: 1200px) {
            --su: 80px;
        }

        @media (min-width: 1600px) {
            --su: 120px;
        }

        // Element sizes

        --article-max-width: 800px;
        --site-max-width: 1600px;

    }

    // Reset and adjusting defaults

    *,
    *:after,
    *:before {
        box-sizing: border-box;
        margin: 0;
        padding: 0;
    }

    img {
        display: block;
        width: 100%;
        max-width: 100%;
    }

    svg {
        display: block;
    }

    video {
        display: block;
        width: 100%;
    }

    // Base page styles

    body {
        background-color: ${props => props.theme.background.normal};
        color: ${props => props.theme.text.normal};
        font-family: var(--family-body);
        font-weight: 400;
        line-height: var(--line-height-heading);
        
    }

    // Base typography styles
    
    a,
    a:visited {
        color: ${props => props.theme.link.normal};
    }

    a:hover {
        color: ${props => props.theme.link.hover};
    }
    
    h1 {
        font-family: var(--family-heading);
        font-size: 64px;
        font-weight: 400;
        line-height: var(--line-height-tight);

        @media screen and (min-width: 800px) {
            font-size: calc(71px + (121 - 71) * ((100vw - 800px) / (1200 - 800)));
        }

        @media screen and (min-width: 1200px) {
            font-size: calc(108px + (158 - 108) * ((100vw - 1200px) / (1600 - 1200)));
        }

        @media screen and (min-width: 1600px) {
            font-size: calc(138px + (258 - 138) * ((100vw - 1600px) / (2560 - 1600)));
        }

        @media screen and (min-width: 2560px) {
            font-size: 258px;
        }

    }

    h2 {
        font-family: var(--family-heading);
        font-size: 80px;
        font-weight: 400;
        line-height: var(--line-height-tight);

        @media (min-width: 800px) {
            font-size: 120px;
        }

    }

    h3 {
        font-family: var(--family-body);
        font-size: 28px;
        font-weight: 500;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 32px;
        }

    }

    h4 {
        font-family: var(--family-body);
        font-size: 20px;
        font-weight: 400;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 24px;
        }

    }

    h5 {
        font-family: var(--family-body);
        font-size: 16px;
        font-weight: 500;
        line-height: var(--line-height-heading);

        @media (min-width: 800px) {
            font-size: 18px;
        }

    }

    h6 {
        font-family: var(--family-body);
        font-size: 15px;
        font-weight: 400;
        letter-spacing: 0.08em;
        line-height: var(--line-height-tight);
        text-transform: uppercase;
    }

    p {
        font-family: var(--family-body);
        font-size: 20px;
        font-weight: 400;
        line-height: var(--line-height-paragraph);

        @media (min-width: 800px) {
            font-size: 24px;
        }

    }

    button {
        background: none;
        border-style: none;
        box-sizing: content-box;
        cursor: pointer;
        display: inline;
        font: inherit;
        height: auto;
        line-height: inherit;
        padding: 0;
        perspective-origin: 0 0;
        text-align: start;
        text-decoration: underline;
        transform-origin: 0 0;
        width: auto;
    }

    blockquote {
        border-left: 1px solid ${props => props.theme.line.normal};
        padding: 8px 32px;
        font-size: 32px;
        line-height: var(--line-height-paragraph);
    }

    pre {
        background: ${props => props.theme.code.background};
        color: ${props => props.theme.code.text};
        padding: 24px;
        font-size: 13px;

        code {
            display: block;
            overflow-x: scroll;
        }

        @media (min-width: 800px) {
            font-size: 18px;
            padding: 32px;
        }

    }

    code {
        background: ${props => props.theme.code.background};
        color: ${props => props.theme.code.text};
        font-size: 13px;

        @media (min-width: 800px) {
            font-size: 18px;
        }


    }

    // Generic .has-shadow class to place a shadow on any element

    .has-shadow {
        box-shadow: 0 32px 32px rgba(0,0,0,0.05), 0 16px 16px rgba(0,0,0,0.05), 0 8px 8px rgba(0,0,0,0.05), 0 4px 4px rgba(0,0,0,0.05), 0 0 2px rgba(0,0,0,0.2);
    }

`
export default GlobalStyles